.GALLERY {
	width: 100%;
	> .inner {
		min-height: 500px;
		button {
			margin: 100px 10px 10px 10px;
			width: 130px;
			height: 40px;
			background: #9d9083;
			color: #fff;
			font: 14px/1 'Noto Sans';
		}

		.frame {
			transform: translateY(200px);
			opacity: 0;
			transition: 1s;

			&.on {
				transform: translateY(0px);
				opacity: 1;
			}

			&::after {
				content: '';
				display: block;
				clear: both;
			}

			article {
				width: 20%;
				float: left;
				padding: 10px;

				> .inner {
					width: 100%;
					background: #fff;
					box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
					padding: 10px;

					.pic {
						width: 100%;
						margin-bottom: 10px;
						cursor: pointer;

						img {
							width: 100%;
						}
					}

					h2 {
						font: normal 14px/1.2 'arial';
						color: #777;
						margin-bottom: 20px;
					}
				}
			}
		}
	}
}
